import { Component, OnInit, ElementRef, ChangeDetectorRef, Inject, EventEmitter, ViewChild } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators, FormArray } from '@angular/forms';
import { ApprovePurchaseOrderService, DateConverterService, CurrentUserService, PoRequisitionDetailMasterService } from '../../../shared';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { jqxWindowComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxwindow';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';

import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-approve-purchase-order',
  templateUrl: './approve-purchase-order.component.html',
  styleUrls: ['./approve-purchase-order.component.scss']
})
export class ApprovePurchaseOrderComponent implements OnInit {

  @ViewChild('msgNotification') msgNotification: jqxNotificationComponent;
  @ViewChild('errNotification') errNotification: jqxNotificationComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('input1') inputEl: ElementRef;
  @ViewChild('myWindow') myWindow: jqxWindowComponent;
  @ViewChild('groupGrid') groupGrid: jqxGridComponent;
  @ViewChild('orderGrid') orderGrid: jqxGridComponent;


  approveReqForm: FormGroup;
  gridSource: any;
  gridDataAdapter: any;
  gridColumns: any = [];
  orderSource: any;
  orderDataAdapter: any;
  orderColumns: any = [];
  editrow: number = -1;
  showDetailsGrid: boolean = false;
  selectedOrderList: Array<any> = [];
  orderName: string;
  userData: any = {};

  constructor(
    private fb: FormBuilder,
    private ars: ApprovePurchaseOrderService,
    private cus: CurrentUserService,
    private dateService: DateConverterService,
    private cdr: ChangeDetectorRef,
    private translate: TranslateService,
    private prdms: PoRequisitionDetailMasterService
  ) {
    this.createForm();
    // this.getTranslation();
    this.userData = this.cus.getTokenData();
  }

  createForm() {
    this.approveReqForm = this.fb.group({
      // 'branchCode': [null, Validators.required],
      'approve_date': [null, Validators.required],
      'approve_by': [null, Validators.required]
    });
  }

  transData: any;
  // getTranslation() {
  //   this.translate.get(['ORDERPENDINGLIST', 'ORDERPENDINGLISTDETAIL', 'SN', 'ORDER', 'DATE', 'NUMBER', 'ORDERBY', 'TYPE', 'SUPPLIER', 'DELIVERYPLACE', 'ITEM', 'NAME', 'SPECIFICATION', 'UNIT', 'ORDERQTY', 'RATE', 'TOTAL', 'INDELIVERYDAY', 'REMARKS', 'ACTION', 'VIEW']).subscribe((translation: [string]) => {
  //     this.transData = translation;
  //   });
  // }

  ngOnInit() {
    this.gridSource =
      {
        datatype: 'json',
        datafields: [
          { name: 'id', type: 'number' },
          { name: 'po_date', type: 'string' },
          { name: 'po_item_requisition_details' },
          { name: 'po_no', type: 'string' },
          { name: 'store_name', type: 'string', map:'store>name' },
          { name: 'store_id', type: 'string' },
          { name: 'vendor_name', type: 'string', map:'vendor>name' },
          { name: 'vendor_id', type: 'string' },
          { name: 'total_amount', type: 'string' },
        ],
        id: 'id',
        localdata: [],
      };

    this.gridDataAdapter = new jqx.dataAdapter(this.gridSource);

    this.gridColumns =
      [
        {
          text: 'SN', sortable: false, filterable: false, editable: false,
          groupable: false, draggable: false, resizable: false,
          datafield: '', columntype: 'number', width: 50,
          cellsrenderer: function (row, column, value) {
            return "<div style='margin:4px;'>" + (value + 1) + "</div>";
          }
        },
        { text: 'PO No.', datafield: 'po_no', },
        { text: 'PO Date', datafield: 'po_date' },
        { text: 'Total Amount', datafield: 'total_amount' },
        { text: 'Store', datafield: 'store_name' },
        { text: 'Vendor', datafield: 'vendor_name', width: 250 },
        {
          text: 'Action', datafield: 'view', sortable: false, filterable: false, width: 85, columntype: 'button',
          cellsrenderer: (): string => {
            return 'View'
          },
          buttonclick: (row: number): void => {
            this.editrow = row;
            let dataRecord = this.groupGrid.getrowdata(this.editrow);
            this.orderName = dataRecord['orderBy']
            if (dataRecord['po_no']) {
              this.jqxLoader.open();
              this.orderSource.localdata = dataRecord['po_item_requisition_details'];
                this.orderGrid.updatebounddata();
                this.jqxLoader.close();
              // this.ars.show(dataRecord['orderNo']).subscribe((res) => {
              //   this.orderSource.localdata = res;
              //   this.orderGrid.updatebounddata();
              //   this.jqxLoader.close();
              // }, (error) => {
              //   this.jqxLoader.close();
              // });
            }
          }
        }
      ];

    this.orderSource =
      {
        datatype: 'json',
        datafields: [
          { name: 'item_name', type: 'string', map:'item>name' },
          { name: 'item_id', type: 'string' },
          { name: 'quantity', type: 'string' },
          { name: 'rate', type: 'number' },
          { name: 'vat', type: 'number' },
          { name: 'value', type: 'number' },
          { name: 'total', type: 'number' },
          // { name: 'inDeliveryDay', type: 'number' },
          { name: 'remarks', type: 'string' }
        ],
        localdata: [],
      };

    this.orderDataAdapter = new jqx.dataAdapter(this.orderSource);

    this.orderColumns =
      [
        {
          text: 'SN', sortable: false, filterable: false, editable: false,
          groupable: false, draggable: false, resizable: false,
          datafield: '', columntype: 'number', width: 50,
          cellsrenderer: function (row, column, value) {
            return "<div style='margin:4px;'>" + (value + 1) + "</div>";
          }
        },
        { text: 'Item Name', datafield: 'item_name', width: 250 },
        { text: 'Quantity', datafield: 'quantity', width: 150 },
        { text: 'Rate', datafield: 'rate', width: 150 },
        { text: 'Vat', datafield: 'vat', width: 150 },
        { text: 'Value', datafield: 'value', width: 150 },
        { text: 'Total', datafield: 'total', width: 150 },
        // { text: this.transData['INDELIVERYDAY'], datafield: 'inDeliveryDay', },
        { text: 'Remarks', datafield: 'remarks' },
      ];
  }

  gridRenderToolbar = (toolbar: any): void => {
    let container = document.createElement('div');
    container.style.margin = '5px';
    let buttonContainer3 = document.createElement('div');
    buttonContainer3.id = 'buttonContainer3';
    buttonContainer3.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer3.innerHTML = "<b>Order Pending List :</b>";
    container.appendChild(buttonContainer3);
    toolbar[0].appendChild(container);
  };

  orderRenderToolbar = (toolbar: any): void => {
    let container = document.createElement('div');
    container.style.margin = '5px';
    let buttonContainer3 = document.createElement('div');
    buttonContainer3.id = 'buttonContainer3';
    buttonContainer3.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer3.innerHTML = "<b>Order Pending List Detail :</b>";
    container.appendChild(buttonContainer3);
    toolbar[0].appendChild(container);
  };


  /**
    * Grid row checked event
    * @param event 
    */
  // rowChange(event: any) {
  //   let dataRecord = this.groupGrid.getrowdata(event.args.rowindex);
  //   let dt = {};
  //   dt['id'] = dataRecord['id'];
  //   this.selectedOrderList.push(dt);
  //   // console.info(this.selectedOrderList);
  // }

  /**
   * Grid row unchecked event
   * @param event 
   */
  // rowUnChange(event: any) {
  //   let dataRecord = this.groupGrid.getrowdata(event.args.rowindex);
  //   if(dataRecord){
  //     let index = this.selectedOrderList.findIndex(x => x.id == dataRecord['id']);
  //     if (index > -1) {
  //       this.selectedOrderList.splice(index, 1);
  //     }
  //   }

  //   console.info(this.selectedOrderList);
  // }

  ngAfterViewInit() {
    this.loadPurchaseData();
    let dateD = this.dateService.getToday();
    // console.info(dateD);
    setTimeout(() => {
      this.approveReqForm.controls['approve_date'].setValue(dateD['fulldate']);
      this.approveReqForm.get('approve_date').markAsTouched();
      this.approveReqForm.controls['approve_by'].setValue(this.userData['user'].username);
      this.approveReqForm.get('approve_by').markAsTouched();
    }, 100);
    this.cdr.detectChanges();
  }

  loadPurchaseData() {
    let getData = { pending : true, limit: 'all' };
    this.jqxLoader.open();
    this.prdms.index(getData).subscribe((res) => {
      // console.log(res);
      this.gridSource.localdata = res;
      this.groupGrid.updatebounddata();
      this.jqxLoader.close();
      // if (res[0]['error']) {
      //   this.gridSource.localdata = [];
      //   this.groupGrid.updatebounddata();
      //   let messageDiv: any = document.getElementById('error');
      //   messageDiv.innerText = res[0]['error'];
      //   this.errNotification.open();
      // }
    }, (error) => {
      this.jqxLoader.close();
    });
  }

  approve(formData) {
    this.jqxLoader.open();
    let id = this.groupGrid.getselectedrowindexes();
    let ids = [];
    for (let i = 0; i < id.length; i++) {
      let dataRecord = this.groupGrid.getrowdata(Number(id[i]));
      // let dt = {};
      // dt['id'] = dataRecord['id'];
      ids.push(dataRecord['id']);
    }
    // formData['status'] = 'Approve';
    formData['approve_by'] = this.userData['user'] && this.userData['user'].username || null ;

    // console.log(ids);
    // console.log(formData);
    if (ids.length > 0 && formData['approve_by'] && formData['approve_date']) {
      formData['approve_requisition'] = true;
      this.prdms.update('['+ids+']',formData).subscribe(
        result => {
          if (result['message']) {
            this.selectedOrderList = [];
            this.loadPurchaseData();
            this.groupGrid.clearselection();
            this.orderSource.localdata = [];
            this.orderGrid.updatebounddata();
            let messageDiv: any = document.getElementById('message');
            messageDiv.innerText = result['message'];
            this.msgNotification.open();
          }
          this.jqxLoader.close();
          if (result['error']) {
            this.groupGrid.clearselection();
            let messageDiv: any = document.getElementById('error');
            messageDiv.innerText = result['error']['message'];
            this.errNotification.open();
          }
        },
        error => {
          this.jqxLoader.close();
          console.info(error);
        }
      );
    } else {
      this.jqxLoader.close();
      let messageDiv: any = document.getElementById('error');
      messageDiv.innerText = "Please Select at least one item";
      this.errNotification.open();
    }
  }

  reject(formData) {
    this.jqxLoader.open();
    formData['array'] = this.selectedOrderList;
    formData['status'] = 'Reject';
    if (formData['array'].length > 0) {
      this.ars.store(formData).subscribe(
        result => {
          if (result['message']) {
            // this.branchChange(this.approveReqForm.controls['branchCode'].value);
            this.selectedOrderList = [];
            this.groupGrid.clearselection();
            this.orderSource.localdata = [];
            this.orderGrid.updatebounddata();
           
            let messageDiv: any = document.getElementById('message');
            messageDiv.innerText = result['message'];
            this.msgNotification.open();
          }
          this.jqxLoader.close();
          if (result['error']) {
            // this.branchChange(this.approveReqForm.controls['branchCode'].value);
            this.selectedOrderList = [];
            this.orderSource.localdata = [];
            this.orderGrid.updatebounddata();
            this.groupGrid.clearselection();
            let messageDiv: any = document.getElementById('error');
            messageDiv.innerText = result['error']['message'];
            this.errNotification.open();
          }
        },
        error => {
          this.jqxLoader.close();
          console.info(error);
        }
      );
    } else {
      this.jqxLoader.close();
      let messageDiv: any = document.getElementById('error');
      messageDiv.innerText = "Please Select at least one item";
      this.errNotification.open();
    }
  }
}



